import './scss/style.scss';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import $ from 'jquery';
import { principleItems } from './js/principle.js';

let arrow = $('.up_arrow')[0];

if ($(".mfp-content")[0] != undefined) {

var firstSelection = orderForm.type;
var secondSelection = orderForm.complect;
var orderingForm = $(".mfp-content")[0];
let firstPrice =  $('.tariff-right-calc__price')[0];
let secondPrice =  $('.tariff-right-calc__price')[2];

document.onload = () => {
    reset();
}

firstSelection.onchange = () => {
    setFirstPrice(firstSelection.selectedIndex);
}

secondSelection.onchange = () => {
    setSecondPrice(secondSelection.selectedIndex);
}

$('.tariff-right__cansel')[0].onclick = reset;

function setFirstPrice(idx) {
    let targetPrice = firstPrice;
    if (idx == 0) {
        targetPrice.innerText = '1000 ₽/мес';
    } else {
        targetPrice.innerText = '1500 ₽/мес';
    }
}

function setSecondPrice(idx) {
    let targetPrice = secondPrice;
    if (idx == 0) {
        targetPrice.innerText = '34 500 ₽/мес';
    } else if (idx == 1) {
        targetPrice.innerText = '44 000 ₽/мес';
    } else {
        targetPrice.innerText = 'от 76 000 ₽/мес';
    }
}

function reset() {
    firstSelection.selectedIndex = 0;
    secondSelection.selectedIndex = 0;
    setFirstPrice(0);
    setSecondPrice(0);
}

$(".close-button")[0].onclick = () => {
    orderingForm.style.display = 'none';
}

$(".tariff-right__button")[0].onclick = (e) => {
    e.preventDefault();
    let complect;
    orderingForm.style.display = 'grid';
    $('.pColor')[2].innerText = firstPrice.innerText;
    $('.pColor')[6].innerText = firstPrice.innerText;
    $('.pColor')[3].innerText = secondPrice.innerText;
    switch (secondSelection.selectedIndex) {
        case 0: complect = "Начальный"; break;
        case 1: complect = "Оптимальный"; break;
        case 2: complect = "Индивидуальный"; break;
    }
    $(".complect")[0].innerText = complect;
}

$('.flat-icons')[0].onclick = (e) => {
    selectIcon(e)
}

if (document.documentElement.scrollWidth < 657)
        makeIconGray(0)

window.onresize = () => {
    if (document.documentElement.scrollWidth < 657)
        makeIconGray(0)
    else {
        for (let i = 0; i < 6; i++) {
            $('.protection-items-item').children()[i*3].classList.remove('unselected');
        }
    }
};

$(".protection-items")[0].onclick = (e) => {
    if (document.documentElement.scrollWidth < 657)
    {
        let idx = idxOf(e, $('.protection-items-item img'));
        makeIconGray(idx);
        $('.protection-items-item__title-hidden')[0].innerText = $('.protection-items-item__title')[idx].innerText;
        $('.protection-items-item__text-hidden')[0].innerText = $('.protection-items-item__text')[idx].innerText;
    }
}

function makeIconGray(idx) {
    for (let i = 0; i < 6; i++) {
        $('.protection-items-item').children()[i*3].classList.add('unselected');
        $('.protection-items-item').children()[idx*3].classList.remove('unselected');
    }
}

function selectIcon(e) {
    let idx = idxOf(e, $('.flat-icons__item'));
    recolor(idx);
    $('.flat-main-descr__img')[0].src = principleItems[idx].img;
    $('.flat-main-descr-text__title')[0].innerText = principleItems[idx].title;
    $('.flat-main-descr-text__text')[0].innerText = principleItems[idx].description;
    $('.flat-main-sheme__pin')[0].style.left = principleItems[idx].coord.x;
    $('.flat-main-sheme__pin')[0].style.top = principleItems[idx].coord.y;
}



function recolor(idx) {
    for (let i = 0; i < 3; i++) {
        $('.flat-icons__item')[i].src = icons[i];
        if (i === idx)
            $('.flat-icons__item')[i].src = activeIcons[i];
    }
}



const icons = [fire, gas, key];
const activeIcons = [fireActive, gasActive, keyActive];
} else if ($('.pay-form-body-col__field')[0] != undefined) {
    

$('.pay-form-body-col__field')[0].onfocus = () => {
    let name =  $('.pay-form-body-col__name')[0];
    
    name.style.top = "0px";
}

$('.pay-form-body-col__field')[1].onfocus = () => {
    let name =  $('.pay-form-body-col__name')[1];
    
    name.style.top = "0px";
}

$('.pay-form-body-col__field')[0].onblur = () => {
    let name =  $('.pay-form-body-col__name')[0];
    
    name.style.top = "25px";
}

$('.pay-form-body-col__field')[1].onblur = () => {
    let name =  $('.pay-form-body-col__name')[1];
    
    name.style.top = "25px";
}


}

function idxOf(e, classArr) {
    let idx = classArr.index(e.target);
    if (~idx){
        return idx;
    }
}

document.onscroll = () => {
    (window.pageYOffset < 1200) ? arrow.style.display = 'none' : arrow.style.display = 'grid'
}

$('.nav-burger__img')[0].onclick = () => {
    if ($('.nav-burger-menu')) {
        $('.nav-burger-menu')[0].classList.toggle('nav-burger-menu-hidden')
    }
}

$('.nav-burger-menu')[0].onclick = (e) => {
    if (~idxOf(e, $('.header-bottom-list__item-burg')))
        $('.nav-burger-menu')[0].classList.toggle('nav-burger-menu-hidden');
}

